<template>
  <div :class="[prefix +'-special-tab', { [prefix+'-special-tab-line']: line }]">
    <div
      v-for="(item, index) in data"
      :key="index"
      :class="[
        seletedIndex == index ? `tab${index}1` : `tab${index}0`,
        'tab',
        { 'tab-nofirst': index > 0 },
      ]"
      @click="tabClick(index)"
    >
      <span :class="{ 'tab-txt': opacity != 1 }">{{ item }}</span>
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent, PropType, ref } from 'vue'
import { usePrefix } from '../../utils/util'


export default defineComponent({
  name: 'SpecialTab',
  props: {
    data: {
      type: Array as PropType<string[]>,
      default: () => [],
    },
    opacity: {
      type: Number,
      default: 1,
    },
    line: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['on-change'],
  setup(props,{ emit }) {
    const { prefix } = usePrefix()
    const seletedIndex = ref(0)
    const tabClick = index => {
      seletedIndex.value = index
      emit('on-change', index)
    }
    return {
      prefix,
      seletedIndex,
      tabClick,
    }
  },
})
</script>
